import { NgModule, Input } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsDependenciesModule } from 'src/app/forms-dependencies/forms-dependencies.module';
import { SharedModule } from '../../shared/shared.module';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/Input';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatButtonModule } from '@angular/material/button';
import { MatTabsModule } from '@angular/material/tabs';
import { MatSelectModule } from '@angular/material/select';
import { MatMenuModule } from '@angular/material/menu';
import { MatIconModule } from '@angular/material/icon';
import { ProfileRoutingModule } from './profile-routing.module';
import { InfoPersonalComponent } from './components/info-personal/info-personal.component';
import { MyRequestsComponent } from './components/my-requests/my-requests.component';
import { ProfileeditComponent } from './components/profileedit/profileedit.component';
import { MyOrdersComponent } from './components/my-orders/my-orders.component';
import { TrackMyorderComponent } from './components/track-myorder/track-myorder.component';
import { MatStepperModule } from '@angular/material/stepper';
import { ChangepasswordComponent } from './components/changepassword/changepassword.component';
import { MatDialogModule } from '@angular/material/dialog';
import { AddressManageComponent } from './components/address-manage/address-manage.component';
import { HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
@NgModule({
  declarations: [
    InfoPersonalComponent,
    MyRequestsComponent,
    ProfileeditComponent,
    MyOrdersComponent,
    TrackMyorderComponent,
    ChangepasswordComponent,
    AddressManageComponent,
    // confirmEquelValidator,
    // HeaderComponent,
    // RegisterNormalUserComponent
  ],
  imports: [
    CommonModule,
    ProfileRoutingModule,
    FormsDependenciesModule,
    MatIconModule,
    MatMenuModule,
    MatInputModule,
    MatCheckboxModule,
    MatButtonModule,
    MatTabsModule,
    MatSelectModule,
    SharedModule,
    MatStepperModule,
    MatDialogModule,
    TranslateModule.forChild({
      loader: {
        provide: TranslateLoader,
        useFactory: httpTranslateLoader,
        deps: [HttpClient]
      }
    })

  ],
  exports: [
    MatFormFieldModule,
    MatInputModule,
    MatCheckboxModule,
    MatButtonModule,
    MatTabsModule,
    MatSelectModule,
  ],
  entryComponents: [
    ChangepasswordComponent,
    ProfileeditComponent,
    AddressManageComponent
  ]

})
export class ProfileModule { }

// AOT compilation support
export function httpTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http);
}